<template>
  <div class="template-container">
    <div class="template-container-title">账号编辑</div>

    <el-form ref="form" :model="form" :inline="true" label-width="100px">
      <!--广告组 begin -->
      <div class="temp-item-block">
        <div class="temp-item-main">
          <el-form-item label="账号名称">
            <el-input clearable v-model="form.account_name"></el-input>
          </el-form-item>
          <div>
            <el-form-item label="QQ">
              <el-input clearable v-model="form.qq"></el-input>
            </el-form-item>
          </div>
          <div>
            <el-form-item label="广点通账户ID">
              <el-input :disabled="true" v-model="form.account_id"></el-input>
            </el-form-item>
          </div>
        </div>

      </div>
      <!--广告组 end -->

      <el-form-item>
        <el-button type="primary" @click="onSubmit">确认修改</el-button>
        <el-button @click="onCancel">取消</el-button>
      </el-form-item>
    </el-form>
  </div>

</template>

<script>
export default {
  created: function() {
    var params = this.$route.params || {}
    if (params.hasOwnProperty('id')) {
      this.getAccount(params.id)
    } else {
      this.$router.push('/setting/accountmanage')
    }
  },
  data() {
    return {
      form: {
        id: 0,
        account_id: '',
        account_name: '',
        qq: ''
      }
    }
  },
  mounted() {
    //console.log(this.listData)
  },
  methods: {
    onCancel() {
      this.$router.push('/setting/accountmanage')
    },
    onSubmit() {
      var params = this.form
      var that = this
      this.$axios
        .get('/Admin/Marketing_Account/edit', {
          params: params
        })
        .then(function(response) {
          var respData = response.data
          if (respData.code == 0) {
            that.$message({
              type: 'success',
              message: respData.msg
            })
            setTimeout(function() {
              that.$router.push('/setting/accountmanage')
            }, 500)
          }else{
            that.$message.error(respData.msg)
          }
        })
        .catch(function(err) {
          that.$message('网络繁忙，请稍后再试！')
          console.log(err)
        })
    },
    // 获取账户
    getAccount(id) {
      var that = this
      this.$axios
        .get('/Admin/Marketing_Account/get', {
          params: { id: id }
        })
        .then(function(response) {
          var respData = response.data
          if (respData.code == 0) {
            if (respData.data[0]) {
              // console.log(respData.data)
              that.form.id = id
              that.form.account_id = respData.data[0]['account_id']
              that.form.account_name = respData.data[0]['account_name']
              that.form.qq = respData.data[0]['qq']
            } else {
              that.$message('账号不存在')
            }
          } else {
            that.$message(respData.msg)
          }
        })
        .catch(function(err) {
          that.$message('网络繁忙，请稍后再试！')
          console.log(err)
        })
    }
  }
}
</script>


<style scoped>
.temp-radio-item {
  width: 100%;
  margin: 0 0 20px 0;
}
.temp-radio-remark {
  display: inline-block;
  color: #999;
  font-size: 14px;
  margin-left: 100px;
}

.radio-inlineblock {
  display: inline-block;
  margin-left: 20px;
}
</style>



